import sellProd from './components/sellProd.vue'
import buyProd from './components/buyProd.vue'
import singleProd from './components/singleProd.vue'
import buyProdSeller from './components/buyProdSeller.vue'
import myProducts from './components/myProducts.vue'

export default[
  {path: '/sell', component: sellProd},
  {path: '/shop', component: buyProd},
  {path: '/product/:id', component:singleProd},
  {path: '/seller/:name', component:buyProdSeller},
  {path: '/myproducts', component:myProducts}
]
