<template>
    <div id="full">
        <div id="add-product">
            <h2>Add new product</h2>
                <form id="add-product-form">
                    <label>Product Name:</label>
                    <input type="text" v-model.lazy="product.name" required placeholder="Type name..."> 
                    <label>Description:</label>
                    <textarea v-model="product.desc" placeholder="Type description...."></textarea>
                    <label>Price:</label>
                    <input type="number" v-model.lazy="product.price" min="0.00" max="10000.00" step="0.01" required>
                    <span>UAH</span>
                    <label>Items for sell:</label>
                    <input type="number" v-model.lazy="product.cnt" min="0" step="1" required>
                    <label>Category:</label>
                    <select v-model="product.category">
                        <option :key="category" v-for="category in categories">{{category}}</option>
                    </select> 
                    <label>Seller Name:</label>
                    <input id="seller" type="text" v-model.lazy="product.seller" required>       
                        <label>Image:</label>
                        <input type="file" @change="onFileChanged"><br>
                      <!-- <button @click="onUpload">Upload!</button> -->
                        
                    
                    <button @click.prevent="submit">Submit</button>
                </form>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import {db, auth} from '../init-firebase'
import account from '../account'
export default {
    data(){
        return{
            product:{
                name:"",
                desc:"",
                price: "",
                cnt:"",
                category:"",
                seller:"",
                imgUrl:""
                             
            },
            categories:[
                "Cactuses",
                "Succulents",
                "Other plants",
                "Pots",
                "Literature",
                "Different"
            ],
            submitted: false,
            selectedFile: null
        }
    },
    methods: {
        submit: function(){
            //const form  = document.querySelector('#add-product-form');
            const formData = new FormData()
            formData.append('file', this.selectedFile,this.selectedFile.name);
            formData.append('upload_preset', 'v0lmrtck');
            this.$http.post('https://api.cloudinary.com/v1_1/dhrzvlk6c/image/upload', formData).then(function(data){
               
              return data.json()
                // this.blogs =  data.body.slice(0,10);
            }).then(function(data){
               // var arrg = JSON.parse(data);
                 console.log( data );
                 this.product.imgUrl = data.url;
                db.collection('cactus-list').add({
                    name:  this.product.name,
                    desc:  this.product.desc,
                    category:  this.product.category,
                    cnt:  this.product.cnt,
                    imgUrl:  this.product.imgUrl,
                    price:  this.product.price,
                    seller:  account.user.displayName,
                    sellerId: account.user.uid,
                    created: new Date()
                    
                })
               //  this.$http.post('https://cactus-fc518.firebaseio.com/cactuslist.json',this.product).then(function(data){
              //  this.$http.post('https://api.cloudinary.com/v1_1/dhrzvlk6c/image/upload',this.product).then(function(data){
               // console.log(data);
               // this.submitted = true;
              //  this.product = "";
             //   this.product.desc = "";
              //  this.product.category = "";
               // this.product.cnt = "";
              //  this.product.imgUrl = "";
              //  this.product.price = "";
             //   this.product.seller = "";
             //   this.selectedFile = "";
            //});
            }).then(function(data){
                    this.submitted = true;
                    this.selectedFile = null;
                    this.product.name = "";
                     this.product.desc = "";
                this.product.category = "";
                this.product.cnt = "";
                this.product.imgUrl = "";
                this.product.price = "";
               this.product.seller = "";
                    
            })
            
            
        },
        onFileChanged (event) {
            this.selectedFile = event.target.files[0];
            
            // this.blogs = blogsArray
        }
        }
    }

    
    

</script>
<style scoped>
#full{
   background: #ebe3e7 ;
   width:70%;
   margin: 0 auto;
   padding: 10px 0;
}
h2{
    text-align: center;
}
form{
    background: #ebe3e7 ;
    margin:0 -60%;
    padding: 20px 20px;
}
form button{
    display: inline-block;
    width: 20%;
        height: 40px;
        margin: 20px 30px;
        padding: 10px;
        text-transform: uppercase;
        border-radius: 5px;
       
        background: #34572a;
        color:#eee;
}
form button:hover{
    border:1px solid #34572a;
        background: #eee;
        color:#34572a;
}
form button:active{
        border:1px solid #34572a;
        background: #eee;
        color:#34572a;
        margin: 22px 32px;
     }
#add-product *{
    box-sizing: border-box;
    

}
#add-product{
    margin: 20px auto;
    max-width: 500px;
    
    
}
label{
    display: block;
    margin: 20px 20px 10px;
}
input[type="text"], textarea{
    display: block;
    width: 100%;
    padding: 8px;margin: 0 auto;
}
input:hover{
    border: 1px solid #34572a;
    background: #e1ff9b ;
}
select:hover{
    border: 1px solid #34572a;
    background: #e1ff9b ;
}
textarea:hover{
    border: 1px solid #34572a;
    background: #e1ff9b ;
}



</style>

