export default{
    computed:{
        filteredProducts: function(){
            this.currentPage = 1;
            const filtered = this.products.filter((product)=>{
                if(this.searchName){
                  return   product.name.toLowerCase().match(this.searchName.toLowerCase()) && product.category.match(this.searchCategory) && product.seller.match(this.searchSeller)
                } else {
                  return   product.name.toLowerCase().match(this.searchGenus.toLowerCase()) && product.category.match(this.searchCategory) && product.seller.match(this.searchSeller)
                }
        })
            
         //  console.log(_.chunk(filtered,16))
          
            return _.chunk(filtered,16)
        
        },
        filteredProductsMy: function(){
            this.currentPage = 1;
            const filtered = this.products.filter((product)=>{
                
                if(this.searchName){
                    return   product.name.toLowerCase().match(this.searchName.toLowerCase()) && product.category.match(this.searchCategory) && product.seller.match(this.searchSeller)
                  } else {
                    return   product.name.toLowerCase().match(this.searchGenus.toLowerCase()) && product.category.match(this.searchCategory) && product.seller.match(this.searchSeller)
                  }
            })
         //  console.log(_.chunk(filtered,16))
          
            return _.chunk(filtered,8)
        
        },
        filteredProducts2: function(){
           
         return this.products.filter((product)=>{
            
            if(this.searchName){
                return   product.name.toLowerCase().match(this.searchName.toLowerCase()) && product.category.match(this.searchCategory) && product.seller.match(this.searchSeller)
              } else {
                return   product.name.toLowerCase().match(this.searchGenus.toLowerCase()) && product.category.match(this.searchCategory) && product.seller.match(this.searchSeller)
              }
         })
        }
    }
}