            let sellerArray=[];
            this.products.forEach((product) => {
                sellerArray.push(product.seller)
            })
            let sellerArrayUnique = [...new Set(sellerArray)]; 
            this.sellers = sellerArrayUnique;
         })
    /*-----------------*/
