            let prodArray = [];
            for(let key in data){
                data[key].id = key
                prodArray.push(data[key])
            }
            this.products = prodArray.reverse();
                let sellerArray=[];
                this.products.forEach((product) => {
                    sellerArray.push(product.seller)
                })
                let sellerArrayUnique = [...new Set(sellerArray)]; 
                this.sellers = sellerArrayUnique;
        })*/

     /*   this.$http.get('https://cactus-fc518.firebaseio.com/cactuslist.json').then(function(data){
          //  console.log(data.json());
           return data.json()
         //  console.log(data.json());
        // this.products =  data.chunk(0,16);
        }).then(function(data){
            let prodArray = [];
           // var sellerArray = [];
            for(let key in data){
                data[key].id = key
                prodArray.push(data[key])
                
           }
             console.log(prodArray);
           // this.products = _.chunk(prodArray,16);
                this.products = prodArray.reverse();
                let sellerArray=[];
                this.products.forEach((product) => {
                    sellerArray.push(product.seller)
                })
                let sellerArrayUnique = [...new Set(sellerArray)]; 
               
                this.sellers = sellerArrayUnique;
       
        }) */
    },
    computed:{
       pgCnt:function(){
           return this.products.length
           
       },
       totalCnt:function(){
          // let i=0;
          // this.products.forEach(function(product) {
          //      i++
          //  });
           return this.filteredProducts2.length
           // return i

       }
       
    }/*,
    filters:{
        'to-uppercase': function(value){
            return value.toUpperCase()
        }
    },
    directives:{
        rainbow:{
            bind(el,binding,vnode){
                el.style.color= "#"+ Math.random().toString().slice(2,8);
            }
        }
    }*/,
    mixins:[
