<style scoped>
    #pagination{
        text-align: center;
        display: block;
    }
     #pagination ul{
      
        list-style-type: none;
        padding: 5px;
        margin: 5px auto;
      
        display: inline-block;
    }   
    #pagination ul li{
         display: inline;
         background: red;
    }
    #show-products{
        max-width:70%;
        min-height: 680px;
        max-height: 1600px;
        
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 3fr;
        background: #ebe3e7;
    }
    #search-bar{
        grid-template: 1/2;
        padding: 10px 20px;
        border-right:1px solid #34572a;
        border-left:1px solid #34572a;
        
    }  
    #search-bar h1:after{
        clear:both;
        display:block;
        content:"";
        margin: 10px;
        padding: 10px;
    } 
    #search-bar label{
        margin: 10px;
        padding: 10px;
    } 
    #search-bar input, #search-bar select{
        display:block;
        width: 80%;
        margin: 10px;
        padding: 10px;

    }

    
    #product-list{
        padding: 10px 10px;
        border-right:1px solid #34572a;
    } 
   
     .single-product{
        padding: 10px;
        margin: 10px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px solid #34572a;
        border-radius: 5px; 
        overflow:hidden;  
        text-overflow:ellipsis;
        
    }
    .single-product:hover{
         background: #f6ffe0;
    }

    #img{
        width:100%;
        height: 60%;
    }
    .imgBack{
            background-image: url('../assets/logo.png');
            background-repeat: no-repeat;
            
    }
    

    #img img{
        box-sizing: border-box;
        width:100%;
        height:100%;
        padding: 0 0;
        margin: 0 auto;
        border-radius: 5px;
        border:1px solid #34572a;
        object-fit: cover;
    }

    #desc{
        width: 100%;
        height: 40%; 
          margin: 5px auto; text-align: center;
    }
    #desc p{
       height: 15px;
        width: 100%;
        margin: 5px auto;
       
        text-align: center;
        word-break: keep-all;
        
      
    }
    
    
    #product-list #products{
        display: grid;
        grid-template-columns: repeat(4,1fr);
         grid-auto-rows: minmax(350px,350px);
        grid-column-gap:15px;
        padding: 5px;
        
        margin: 0 10px;
     }
     .prodName{
         font-size: 14px;
         text-transform: uppercase;
         font-weight: bold;
     } 
     .prodName:hover{
         font-size: 14px;
        color: #359b19;
     }   
     a{
         color: #000000;
         text-decoration: none;
     }
     #resetSearch{
        display:block;
        width: 40%;
        height: 40px;
        margin: 20px 30px;
        padding: 10px;
        text-transform: uppercase;
        border-radius: 5px;
       
        background: #34572a;
        color:#eee;
     }
     #resetSearch:hover{
        border:1px solid #34572a;
        background: #eee;
        color:#34572a;
     }
     #resetSearch:active{
        border:1px solid #34572a;
        background: #eee;
        color:#34572a;
        margin: 22px 32px;
     }
        
        

    
    

   

    

</style>