<template>
    <div>
    <div id="topline">
        
        <div id="auth-bar">
            <ul>    
                    <li>
                        <img id="cart-img" src="../assets/cart_empty.png" alt="Cart">
                    </li>
             </ul> 
                  <ul> 
                    <li>
                        <router-link to="/22222" active-class="sign-bar" exact>Cart</router-link>
                    </li>
                    <li class="logged-out" v-show="!userLoged"><!--модал логина-->
                    <Modal v-model="modalLogin" class ="myclass" >
                            <form id="login-form">
                                <div id="login-inputs">
                                    <label for="login-email">Email:</label>
                                    <input type="email" id="login-email" />
                                    <label for="login-password">Password:</label>
                                    <input type="password" id="login-password" />
                                </div>
                                <div id="login-buttons">
                                    <button id="login-close" @click="closeModalLogin">Cancel</button>
                                    <button id="login-login" @click="login">Login</button>
                                </div>
                                <div id="login-signup-button">
                                    <!--<router-link to="/shop" active-class="sign-bar" exact><span @click="closeModalLogin">Sign up</span></router-link>-->
                                    <!--закрываем окно логина, открываем форму регистрации-->
                                    <a href="#" @click="modalSignUp = true; closeModalLogin()">Sign up</a>
                                </div>

                            </form>
                        <div slot="footer" />
                        </Modal>
                        <a href="#" @click="modalLogin = true">Login</a>
                    </li>
                    <li class="logged-out" v-show="!userLoged"><!--модал регистрации-->
                    <Modal v-model="modalSignUp" class ="myclass" >
                        <form id="signup-form">
                                <div id="signup-inputs">
                                    <label for="signup-username">Username:</label>
                                    <input type="username" id="signup-username" />
                                    <label for="signup-email">Email:</label>
                                    <input type="email" id="signup-email" />
                                    <label for="signup-password">Password:</label>
                                    <input type="password" id="signup-password" />
                                </div>
                                <div id="signup-buttons">
                                    <button id="signup-close" @click="closeModalSignUp">Cancel</button>
                                    <button id="signup-signup" @click="signUp">Sign up</button>
                                </div> 
                                <div id="signup-login-button">
                                    <!--<router-link to="/shop" active-class="sign-bar" exact><span @click="closeModalSignUp">Login</span></router-link>-->
                                    <!--закрываем форму регистрации, открываем окно логина-->
                                    <a href="#" @click="modalLogin = true; closeModalSignUp()">Login</a>
                                </div>
                            </form>
                        <div slot="footer" />
                        </Modal>
                        <a href="#" @click="modalSignUp = true">Sign up</a>
                    </li>
                    <li class="logged-in">
                        <p v-show="userLoged">Wellcome, </p>
                        <router-link to="/myproducts" v-show="userLoged" active-class="sign-bar" exact>{{userLogedName}}</router-link>
                    </li>
                    <li class="logged-in">
                        <router-link to="" v-show="userLoged" active-class="sign-bar" exact><span @click="logout">Logout</span></router-link>
                    </li>
            </ul>
       </div>    
    </div>
    <div id="logo">
        <router-link to="/shop" v-show="userLoged" active-class="sign-bar" exact><img src="../assets/logo.png" alt="logo"></router-link>
        
    </div>
    <nav>
       
        <ul>
            <li>
                <router-link to="/" exact>Home</router-link>
            </li>
            <li>
                <router-link to="/shop" exact>Buy</router-link>
            </li>
            <li>
                <router-link to="/sell" v-show="userLoged" exact>Sell</router-link>
            </li>
            <li>
                <router-link to="/rules" exact>Rules</router-link>
            </li>
        </ul>
    </nav>
    </div>
</template>

<script>
//import
import {db, auth} from '../init-firebase'
import account from '../account'
export default {
    data(){
        return {
            modalLogin:false,
            modalSignUp:false
        }
    },

    methods:{
        closeModalLogin(){
            const loginForm = document.querySelector('#login-form');
            loginForm.reset();
            this.modalLogin = false;
        },
        closeModalSignUp(){
            const signupForm = document.querySelector('#signup-form');
            signupForm.reset();
            this.modalSignUp = false;
        },
        signUp(){
            const signupForm = document.querySelector('#signup-form');
            const email = signupForm['signup-email'].value;
            const password = signupForm['signup-password'].value;
            
            //sign up user
            auth.createUserWithEmailAndPassword(email, password).then(user=> {
                return user.user.updateProfile({
                    displayName: signupForm['signup-username'].value
                }).then(cred=>{
                //console.log(cred);
                    return db.collection('users').doc(user.user.uid).set({
                    username: signupForm['signup-username'].value,
                    email: email
                    }).then(()=>{//перелогиниваемся, чтоб подтянулся displayName
                        auth.signOut()
                    }).then(()=>{
                        auth.signInWithEmailAndPassword(email, password)
                    }).then(()=>{
                    this.modalSignUp = false;
                    });
            })
            //this.authStateChanged();
        })
        },
        login(){
            const loginForm = document.querySelector('#login-form');
            const email = loginForm['login-email'].value;
            const password = loginForm['login-password'].value;  
            auth.signInWithEmailAndPassword(email, password).then(cred=>{
                console.log(cred.user);
            }).then(()=>{
                this.modalLogin = false;
               });  
              // this.authStateChanged();
        },
        logout(){
             auth.signOut().then(()=>{
            console.log('user signOut');
            this.$router.push('/shop')
           
        
            });
           // this.authStateChanged();
        },
        /*authStateChanged(){
            auth.onAuthStateChanged(user=>{
                
                if(user){
                    console.log('user loged in' , user);
                } else{
                    console.log('user logged out')
                }
            })
        }*/
        
        
        
    },
    computed:{
        userLoged(){
           //  console.log(account.user)
             if(account.user){//console.log(true)
                    return true;
             } else{//console.log(false)
                    return false;
             }
           
        },
        userLogedName(){
            if(this.userLoged){
               // console.log(account.user.email)
                return account.user.displayName
            } 
            //else{
             //   console.log('dummy');
          //      return 'dummy'
         //  }

        }
    }
    
    
}
</script>


<style scoped>

#login-form label,#login-form input, #signup-form label, #signup-form input {
    display: block;
    margin: 5px auto;
    padding: 5px 20px;
    font-size: 1.5em;
    width: 90%;
    border-radius: 5px;
}
#login-form #login-close, #login-form #login-login, #signup-form #signup-close, #signup-form #signup-signup{
    display: inline-block;
    width: 30%;
    padding: 10px 20px;
    margin: 0 auto;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: bold;
}
#login-form #login-buttons, #signup-form #signup-buttons{
    text-align: center;
    padding: 10px; 
}
#login-form #login-buttons button, #signup-form #signup-buttons button{
   margin: 0 15px;
}
#login-signup-button, #signup-login-button{
    display: inline-block;
    margin: 0 25px;
}
.ivu-btn-text{
    content: '123';
}
.myclass /deep/ .ivu-icon-ios-close {
    display:none;
}
.myclass /deep/ .ivu-modal-footer {display:none}

#topline{
    width:100%;
    height: 40px;
    background: #d8c896;
    border-bottom: 1px solid #34572a;
    border-top: 1px solid #34572a;
}

#logo{
    width:70%;
    margin: 0 auto;
}
#logo img{
    width: 300px;  
}
#auth-bar{
    display: grid;
    grid-template-columns: repeat(2,max-content);
    align-content: center;
    align-items: center;
    justify-content: right;
    width: 70%;
    margin: 0 auto;
    height: 100%;
    
}
#auth-bar ul{
    display: grid;
    grid-template-columns: repeat(5,max-content);
    
    margin-right: -10px;
    padding: 0;
    justify-self: center;
    height: 100%;
    align-content: center;
    
   
}
#auth-bar li{
    height: 100%;
     display: inline-block;
     padding: 0 5px;
}
#auth-bar li p{
    height: 100%;
     display: inline-block;
     color: #fff;
}

#auth-bar img{
    width: 90%;
    height: 90%;
    margin: 0;
    padding: 0;
    display: inline-block;
  
   
}
#auth-bar img:hover{
    width: 95%;
    height: 95%;
    margin: 0;
    padding: 0;
    display: inline-block;
  
   
}

#auth-bar li a{    
    text-decoration: none; 
    color: #fff; 
   height: 100%;
   padding: 5px;    
   
   
}
#auth-bar li a:hover{   
    color: #34572a;
}



nav ul{
    list-style-type: none;
   grid-column: 1/7;
    
    margin: 0;
} 
nav ul li{
    display: inline-block;
    
    
}

nav ul li a{
    color: #dacdd0;
    text-decoration: none;
    text-transform: uppercase;
    padding: 12px 18px;
    border-radius: 5px;
}
nav ul li a:hover{
    background: #eee;
    color: #34572a;
}
nav{
    background: #34572a;
    border: 2px solid #67861f;
    border-radius: 5px;
    width: 70%;
    padding: 14px 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(6,1fr);grid-gap: 10px;
    
}
.router-link-active{
    background: #eee;
    color: #34572a;
}
.sign-bar{
    background:none;
    height: 100%;
}


</style>
