<template>
    <div id="show-products">
        <div id="search-bar">
              <h1>Search:</h1> 
                <label>By name: </label>
                <input type="text" v-model="searchName" placeholder="search product">
                <label>By category: </label>
                <select v-model="searchCategory">
                        <option :key="category" v-for="category in categories">{{category}}</option>
                </select> 
              <!--  <label>By genus: </label>-->
              <!--  <input type="text" v-model="searchGenus" placeholder="search product">-->
                <label>By seller: </label>
                <select v-model="searchSeller">
                        <option :key="seller" v-for="seller in sellers">{{seller}}</option>
                </select>
                <button id="resetSearch" @click="resetSearch">Reset</button>
        </div>
        <div id="product-list">
                <h1>Manage my products:</h1>
                <button id="addNew" @click="addNew">Add new</button>
                       
            <div id="products">
                <!--<input type="text" v-model="search" placeholder="search product">  -->
            <!--    <div v-for="page in products" class="single-page"> -->
                    <div v-for="product in filteredProductsMy[currentPage-1]" class="single-product">
                        <div id="img">
                            <img :src="product.imgUrl" :class="imgClass(product)"/>
                        </div>
                        <div id="desc">
                            <router-link :to="'/product/'+ product.id">
                                <p class="prodName">{{product.name}}</p>
                            </router-link>
                            <p>{{product.price}}</p>
                            <!--<p>{{product.category}}</p>-->
                            <p>In stock: {{product.cnt}}</p>
                            <!--<p>{{product.id}}</p>-->
                        </div>
                        <div id="manageButtons">
                                <Modal v-model="modalEdit" class ="myclass" ><!--модал удаления-->
                                    <form id="edit-form">
                                        <div id="edit-inputs">
                                            <label for="edit-title">Title:</label>
                                            <input type="text" id="edit-title" v-model="productEdit.name"/>
                                            <label for="edit-desc">Description:</label>
                                            <input type="textarea" id="edit-desc" v-model="productEdit.desc"/>
                                            <label for="edit-price">Price:</label>
                                            <input type="number" id="edit-price" v-model="productEdit.price"/>
                                            <label for="edit-cnt">In stock:</label>
                                            <input type="number" id="edit-cnt" v-model="productEdit.cnt"/>
                                            <label for="edit-category">Category:</label>
                                            <select v-model="productEdit.category">
                                                <option :key="category" v-for="category in categories">{{category}}</option>
                                            </select> 
                                            <label for="edit-img">Image:</label>
                                            <input type="file" @change="onFileChanged">
                                        </div>
                                        <div id="edit-buttons">
                                            <button id="edit-close" @click.prevent="closeModalEdit()">Cancel</button>

                                            <Modal v-model="modalEditResult" class ="myclass" ><!--модал после обновления-->
                                                <form id="edit-result-form">
                                                    <div id="edit-result-inputs">
                                                        <img id="success-img" src="../assets/success.png" v-show="errorCode==0" alt="Success" title="Success">
                                                        <img id="error-img" src="../assets/error.png"  v-show="errorCode==1" alt="Error" title="Error">
                                                        
                                                        <p>{{modalMessage}}</p>
                                                    </div>
                                                    <div id="edit-result-buttons">
                                                        <button id="edit-result-close" @click.prevent="closeModalEditResult">OK</button>
                                                       
                                                    </div>
                                                </form>
                                                <div slot="footer" />
                                            </Modal>
                                            <button id="edit-edit" @click.prevent="editSave">Save</button>

                                        </div>
                                    </form>
                                     <div slot="footer" />
                                </Modal>
                                <img id="edit-img" @click.prevent="modalEditOpen(product)" src="../assets/edit.png" alt="Edit" title="Edit">

                                <Modal v-model="modalDelete" class ="myclass" ><!--модал удаления-->
                                    <form id="delete-form">
                                        <div id="delete-inputs">
                                            <img id="warning-img" src="../assets/warning.png" alt="Warning" title="Warning">
                                            <p>Are you sure you want to delete this element?</p>
                                        </div>
                                        <div id="delete-buttons">
                                            <button id="delete-close" @click.prevent="closeModalDelete()">Cancel</button>
                                            <Modal v-model="modalEditResult" class ="myclass" ><!--модал после обновления-->
                                                <form id="edit-result-form">
                                                    <div id="edit-result-inputs">
                                                        <img id="success-img" src="../assets/success.png" v-show="errorCode==0" alt="Success" title="Success">
                                                        <img id="error-img" src="../assets/error.png"  v-show="errorCode==1" alt="Error" title="Error">
                                                        <p>{{modalMessage}}</p>
                                                    </div>
                                                    <div id="edit-result-buttons">
                                                        <button id="edit-result-close" @click.prevent="closeModalEditResult">OK</button>
                                                       
                                                    </div>
                                                </form>
                                                <div slot="footer" />
                                            </Modal>
                                            <button id="delete-delete" @click.prevent="remove(product.id)">Delete</button>
                                        </div>
                                    </form>
                                     <div slot="footer" />
                                </Modal>
                                <!--<img id="del-img" @click="remove(product.id)"src="../assets/cancel.png" alt="Remove" title="Remove"> -->
                                <img id="del-img" @click="modalDelete = true" src="../assets/cancel.png" alt="Remove" title="Remove"> 
                        </div>
                   </div>
                   
                   
            <!--    </div> -->
                
           </div>
            <div id="pagination">
                    <p v-show="totalCnt==0">No matches</p>
                    <Page v-show="totalCnt>0" v-model="currentPage" @on-change="page=>currentPage=page" :total="totalCnt" :page-size="8"/>
                    <!--    <p>Current page: {{ currentPage }}</p>-->
                    <!--   <v-pagination v-model="currentPage" :page-count="pgCnt"></v-pagination> -->
            </div>
        </div>


  </div>
</template>

<script>
// Imports
import {db, auth} from '../init-firebase'
import searchMixin from '../mixins/searchMixin'
import account from '../account'
//<!--  <router-link :to="'/blog/'+ blog.id"> -->
//<!--  </router-link> -->
//import vPagination from 'vue-plain-pagination'
import _ from 'lodash'
export default {
   // components: { vPagination },
    data () {
        return {
            currentPage: 1,
            searchName:"",
            searchCategory:"",
            searchGenus:"",
            searchSeller:"",
            products:[],
            sellers:[],
            categories:[
                "Cactuses",
                "Succulents",
                "Other plants",
                "Pots",
                "Literature",
                "Different"
            ],
            productEdit:{
                name:"",
                desc:"",
                price: "",
                cnt:"",
                category:"",
                seller:"",
                imgUrl:""
            },
            modalMessage: "",
            errorCode: 0,
            modalEdit: false,
            modalDelete: false,
            modalEditResult: false,
            selectedFile: null
            
          
        }
        
    },
    methods: {
        imgClass(product) {
         return !product.imgUrl ? 'imgBack' : '';
        },
        resetSearch(){
            this.searchName = "",
            this.searchCategory ="",
            this.searchGenus ="",
            this.searchSeller =""
            
        },
        addNew(){

        },
        editSave(){

        /*     const formData = new FormData()
            formData.append('file', this.selectedFile,this.selectedFile.name);
            formData.append('upload_preset', 'v0lmrtck');
            this.$http.post('https://api.cloudinary.com/v1_1/dhrzvlk6c/image/upload', formData).then((data)=>{
               
              return data.json()
                // this.blogs =  data.body.slice(0,10);
            }).then(function(data){
               // var arrg = JSON.parse(data);
                 console.log( data );
                 this.productEdit.imgUrl = data.url; */

            const name = this.productEdit.name;
            const desc = this.productEdit.desc;
            const price = this.productEdit.price;
            const cnt = this.productEdit.cnt;
            const category = this.productEdit.category;
            const id = this.productEdit.id;
          //  const imgUrl = this.productEdit.imgUrl;
            if(!name || !desc || !price || !cnt || !category){
                this.modalMessage = "Impossible update! One or few fields are empty!";
                this.errorCode = 1;
                return this.modalEditResult = true
            }
              //  console.log(id);
            db.collection('cactus-list').doc(id).update({
                
              ...name && {name},
              ...desc && {desc},
              ...price && {price},
              ...cnt && {cnt},
              ...category && {category},
            //  ...imgUrl && {imgUrl}
            }).then((data) => {
                let value = this.userLogedId;
               // console.log(value);
                this.getProd(value);
              //  console.log("Document successfully UPDATED!");
                this.modalMessage = "Product successfully updated!";
                this.modalEdit = false;
                this.modalEditResult = true;
            }).catch(function(error) {
                    console.error("Error updating document: ", error);
                     this.modalMessage = error;
                     this.errorCode = 1;
                 //    this.modalEdit = false,
                     this.modalEditResult = true
            });
          //  })
        },
        remove(productId){
          //  console.log(productId);
            db.collection('cactus-list').doc(productId).delete().then(() => {
                this.modalMessage = "Product successfully deleted!";
                this.modalEditResult = true;
                let value = this.userLogedId;
                console.log(value);
                this.getProd(value);
               // console.log("Document successfully deleted!");
                
               this.modalDelete = false;
                
            }).catch(function(error) {
                    console.error("Error removing document: ", error);
                    this.modalMessage = error;
                     this.errorCode = 1;
                 //    this.modalEdit = false,
                     this.modalEditResult = true;
                     this.modalDelete = false;
            });
        },
        getProd(value){
            db.collection('cactus-list').where('sellerId','==', value).orderBy('created').get().then((data)=>{
         //   console.log(data.docs)
            return data.docs
         }).then((data)=>{
             let prodArray = [];
           /*for(let key in data){
                data[key].id = key
                prodArray.push(data[key])
           }*/
           
           data.forEach(doc => {
              // let key = doc.id;
              // data._key = key
               //doc.data.push(key)
               let docData = doc.data();
               docData.id=doc.id;
              // console.log(docData)

                   prodArray.push(docData)
            })

          // console.log(prodArray)
            this.products = prodArray.reverse();
            let sellerArray=[];
            this.products.forEach((product) => {
                sellerArray.push(product.seller)
            })
            let sellerArrayUnique = [...new Set(sellerArray)]; 
            this.sellers = sellerArrayUnique;
         })
         /*-----------------*/
     /*   }).then((data)=>{
            let prodArray = [];
            for(let key in data){
                data[key].id = key
                prodArray.push(data[key])
            }
            this.products = prodArray.reverse();
                let sellerArray=[];
                this.products.forEach((product) => {
                    sellerArray.push(product.seller)
                })
                let sellerArrayUnique = [...new Set(sellerArray)]; 
                this.sellers = sellerArrayUnique;
        })*/

     /*   this.$http.get('https://cactus-fc518.firebaseio.com/cactuslist.json').then(function(data){
          //  console.log(data.json());
           return data.json()
         //  console.log(data.json());
        // this.products =  data.chunk(0,16);
        }).then(function(data){
            let prodArray = [];
           // var sellerArray = [];
            for(let key in data){
                data[key].id = key
                prodArray.push(data[key])
                
           }
             console.log(prodArray);
           // this.products = _.chunk(prodArray,16);
                this.products = prodArray.reverse();
                let sellerArray=[];
                this.products.forEach((product) => {
                    sellerArray.push(product.seller)
                })
                let sellerArrayUnique = [...new Set(sellerArray)]; 
               
                this.sellers = sellerArrayUnique;
       
        }) */
        },
        closeModalDelete(){
         //  e.preventDefault();
         //  const deleteForm = document.querySelector('#delete-form');
         //  deleteForm.reset();
           
            this.modalDelete = false;
          //  this.modalEdit = false;
        },
        closeModalEdit(){
         //  e.preventDefault();
         //  const deleteForm = document.querySelector('#delete-form');
         //  deleteForm.reset();
           
            this.modalEdit = false;
          //  this.modalEdit = false;
        },
        closeModalEditResult(){
                this.closeModalEditResult_1().then(()=>{
                this.errorCode=0;
            })    
        },
        async closeModalEditResult_1(){
            this.modalEditResult=false;
            this.modalMessage="";  
        },
        onFileChanged (event) {
            this.selectedFile = event.target.files[0];   
        },
        modalEditOpen(product){
            this.productEdit.name = product.name;
            this.productEdit.desc = product.desc;
            this.productEdit.price = product.price;
            this.productEdit.cnt = product.cnt;
            this.productEdit.category = product.category;
            this.productEdit.id = product.id;
            this.modalEdit = true
        } 
    
    },
    created(){
        let value = this.userLogedId;
        if(value){
            this.getProd(value);
        }
    },
    watch: {userLogedId(value){
        if(value){
            this.getProd(value);
        }
     }
    },
    computed:{
       pgCnt:function(){
           return this.products.length
           
       },
       totalCnt:function(){
          // let i=0;
          // this.products.forEach(function(product) {
          //      i++
          //  });
           return this.filteredProducts2.length
           // return i

       },
       userLoged(){
           //  console.log(account.user)
             if(account.user){//console.log(true)
                    return true;
             } else{//console.log(false)
                    return false;
             }
           
        },
        userLogedId(){
            if(this.userLoged){
               // console.log(account.user.email)
                return account.user.uid
            } 
            //else{
             //   console.log('dummy');
          //      return 'dummy'
         //  }

        }
       
    },/*
    filters:{
        'to-uppercase': function(value){
            return value.toUpperCase()
        }
    },
    directives:{
        rainbow:{
            bind(el,binding,vnode){
                el.style.color= "#"+ Math.random().toString().slice(2,8);
            }
        }
    },*/
    mixins:[
        searchMixin
    ]
}
</script>

<style scoped>
#edit-form input, #edit-form label, #edit-form select {
    display: block;
    margin: 5px auto;
    padding: 5px 20px;
    font-size: 1.5em;
    width: 90%;
    border-radius: 5px;
}
#delete-form p, #edit-result-form p {
    display: block;
    margin: 5px auto;
    padding: 5px auto;
    font-size: 1.5em;
    width: 90%;
    border-radius: 5px;
    text-align: center; 
    align-self:center;
}
#delete-form #delete-close, #delete-form #delete-delete, #edit-form #edit-close, #edit-form #edit-edit, #edit-result-form #edit-result-close{
    display: inline-block;
    width: 30%;
    padding: 10px 20px;
    margin: 0 auto;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: bold;
}
#delete-form #delete-buttons, #edit-form #edit-buttons, #edit-result-form #edit-result-buttons{
    text-align: center;
    padding: 10px; 
}
#delete-form #delete-buttons button, #edit-form #edit-buttons button, #edit-result-form #edit-result-buttons button{
   margin: 0 15px;
}
#edit-result-form #edit-result-inputs, #delete-form #delete-inputs{
    display: grid;
    grid-template-columns: 12% 88%;
    
    
}
#edit-result-inputs img, #delete-inputs img{
    align-self:center;
    width: 100%;
    margin-left: 15px;
   
}

.myclass /deep/ .ivu-icon-ios-close {
    display:none;
}
.myclass /deep/ .ivu-modal-footer {
    display:none
}
#manageButtons{
    margin: 5px;
}
    #manageButtons img{
    width: 70%;
    height: 50%;
    margin: 0;
    padding-left: 5px;
    display: block;
    object-fit:contain;
}
#manageButtons img:hover{
    width: 70%;
    height: 50%;
    padding-left: 0;
    
    
}
    #pagination{
        text-align: center;
        display: block;
    }
     #pagination ul{
      
        list-style-type: none;
        padding: 5px;
        margin: 5px auto;
      
        display: inline-block;
    }   
    #pagination ul li{
         display: inline;
         background: red;
    }
    #show-products{
        max-width:70%;
        min-height: 680px;
        max-height: 2000px;
        
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 3fr;
        background: #ebe3e7;
    }
    #search-bar{
        grid-template: 1/2;
        padding: 10px 20px;
        border-right:1px solid #34572a;
        border-left:1px solid #34572a;
        
    }  
    #search-bar h1:after{
        clear:both;
        display:block;
        content:"";
        margin: 10px;
        padding: 10px;
    } 
    #search-bar label{
        margin: 10px;
        padding: 10px;
    } 
    #search-bar input, #search-bar select{
        display:block;
        width: 80%;
        margin: 10px;
        padding: 10px;

    }

    
    #product-list{
        display: block;
        padding: 10px 10px;
        border-right:1px solid #34572a;
    } 
       
     .single-product{
         display: grid;
         grid-template-columns: 25% 68% 7%;
         grid-auto-rows: minmax(210px,210px);
       
        
        margin: 5px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px solid #34572a;
        border-radius: 5px; 
        overflow:hidden;  
        text-overflow:ellipsis;
        
    }
    .single-product:hover{
         background: #f6ffe0;
    }

    #img{
        width:100%;
        height: 100%;
        padding: 5px;
       
    }
    .imgBack{
            background-image: url('../assets/logo.png');
            background-repeat: no-repeat;
            
    }
    

    #img img{
        box-sizing: border-box;
        width:100%;
       height: 100%;
        padding: 0 0;
        margin: 0 auto;
        border-radius: 5px;
        border:1px solid #34572a;
        object-fit: cover;
    }

    #desc{
        width: 100%;
        height: 40%; 
          margin: 5px auto; text-align: center;
    }
    #desc p{
       height: 15px;
        width: 100%;
        margin: 5px auto;
       
        text-align: center;
        word-break: keep-all;
        
      
    }
    
    
    #product-list #products{
        display: grid;
        grid-template-columns: repeat(1,1fr);
         grid-auto-rows: minmax(220px,220px);
        
        padding: 5px;
        
        margin: 0 10px;
     }
     .prodName{
         font-size: 14px;
         text-transform: uppercase;
         font-weight: bold;
     } 
     .prodName:hover{
         font-size: 14px;
        color: #359b19;
     }   
     a{
         color: #000000;
         text-decoration: none;
     }
     #resetSearch{
        display:block;
        width: 40%;
        height: 40px;
        margin: 20px 30px;
        padding: 10px;
        text-transform: uppercase;
        border-radius: 5px;
        background: #34572a;
        color:#eee;
     }
     #resetSearch:hover{
        border:1px solid #34572a;
        background: #eee;
        color:#34572a;
     }
     #resetSearch:active{
        border:2px solid #34572a;
        background: #eee;
        color:#34572a;
        /*margin: 21px 31px;*/
     }

      #addNew{
        display:inline-block;
        width: 25%;
        height: 40px;
        margin: 20px 30px;
        padding: 10px;
        text-transform: uppercase;
        border-radius: 5px;
        background: #34572a;
        color:#eee;
     }
     #addNew:hover{
        border:1px solid #34572a;
        background: #eee;
        color:#34572a;
     }
     #addNew:active{
        border:2px solid #34572a;
        background: #eee;
        color:#34572a;
        
     }
        
        

    
    

   

    

</style>